import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Dropdown } from 'primereact/dropdown';
import MyColorPicker from './MyColorPicker';

class IndicatorForm extends Component {
  state = {
    dataItem: undefined,
    labelColor: '',
    valueColor: '',
    backgroundColor: ''
  }

  static defaultProps = {}

  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItem, labelColor, valueColor, backgroundColor } = tile.properties;
      const newDi = this.dataItems.filter((di) => di.label === dataItem.label);
      this.setState({
        dataItem: newDi[0]
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { dataItem, labelColor, valueColor, backgroundColor } = this.state;
    return (
      <section className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          optionLabel={'label'}
          name='dataItem'
          value={dataItem}
          onChange={this.handleChange}
          options={this.dataItems}
          placeholder='Select a data item'
          filter={true}
          filterBy='label'
          required={true}
        />
        <MyColorPicker
          color={labelColor}
          handleChange={this.handleChange}
          label='Label Color'
          name='labelColor'
        />
        <MyColorPicker
          color={valueColor}
          handleChange={this.handleChange}
          label='Value Color'
          name='valueColor'
        />
        <MyColorPicker
          color={backgroundColor}
          handleChange={this.handleChange}
          label='Background'
          name='backgroundColor'
        />
      </section>
    )
  }
}

function mapStateToProps({ data, ui }) {
  return { data, ui }
}

export default connect(mapStateToProps)(IndicatorForm);
